export default {
	data() {
		return {
			invitaionCode:"",
			type: 2,
			tempFilePath:"",
			src:""
		}
	},
	onLoad(e) {
		let that=this;
		uni.getStorage({
		 	key:"shopObj",
		 	success(res){
		 		let shopObj=JSON.parse(res.data);
				shopObj.merchantId+=""
				var num="";
				for(let i=0,len=8-shopObj.merchantId.length;i<len;i++){
					num+=0
				}
				num+=shopObj.merchantId;
				that.invitaionCode=num;
		 		that.src="https://wx.hnlxyj.com/user/share/qrCode?block=1&width=800&height=800&url=http://www.hnlxyj.com/wx/register/register.html?marchantId="+shopObj.merchantId
		 	}
		})
		this.type=e.type
	},
	onShow(){
		console.log(this.type)
	},
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url: "/pages/user/userList1"
		})
	},
	methods: {
		copy(){
			uni.setClipboardData({
				data: this.invitaionCode,
				success: function () {
					console.log('success');
				}
			});
		},
		service() {
			uni.navigateTo({
				url: "/pages/enter/service"
			})
		},
		save() {
			uni.showLoading({
				title: '图片保存中...',
			})
			var that=this;
			var urlP="";
			var x=0,y=0;
			if(this.type==0){
				urlP="../../static/invite/invite_3.png";
				x=80;
				y=180;
			}else{
				urlP="../../static/invite/invite_4.png"
				x=80;
				y=210;
			}
			var url=that.src;
			const context = uni.createCanvasContext('myCanvas')
			context.drawImage(urlP, 0, 0, 274, 363)
			context.drawImage(url, x, y, 90, 90);
			context.setFillStyle('white');
			context.setFontSize(14);
			context.fillText("邀请码:"+that.invitaionCode, 80,326)
			context.draw(false, function() {
				uni.canvasToTempFilePath({
					canvasId: 'myCanvas',
					success: function(res) {
						that.tempFilePath=res.tempFilePath;
						uni.getImageInfo({
							src: that.tempFilePath,
							success: function(image) {
								uni.saveImageToPhotosAlbum({
									filePath: image.path,
									success: function() {
										uni.hideLoading()
										uni.showToast({
											title: '图片保存成功',
											icon: 'none',
											duration: 2200
										});
									},
									fail: function() {
										uni.hideLoading()
										uni.showToast({
											title: "保存失败，请稍后重试",
											icon: "none"
										});
									}
								});
							}
						});
					}
				})
			})			
		}
	}
}
